/*HEADER*/
.main-navbar {
	padding: 37px 60px 37px;
	min-height: 124px;
	border: none;
	margin-bottom: 0;
	background: $putih;
}
@media (max-width: 960px){
	.main-navbar {
		padding: 37px 20px 37px;
	}
}
@media (max-width: 845px){
	.main-navbar {
		padding: 37px 10px 37px;
	}
}
@media (max-width: 767px){
	.main-navbar {
		padding: 37px 10px 17px;
		text-align: center;
	}
}

@media (max-width: 1030px) {
	.main-navbar .navbar-right {
		float: left !important;
	}
}
@media (max-width: 767px) {
	.main-navbar .navbar-right {
		float: none !important;
	}
}
.main-navbar .navbar-nav > li {
	margin: 0 12px;
}
@media (max-width: 1220px) {
	.main-navbar .navbar-nav > li {
		margin: 0 0px;
	}
}
@media (max-width: 1030px) {
	.main-navbar .navbar-nav {
		margin: 0 auto;
		width: 790px;
		margin-top: 80px;
		float: none !important;
		padding-left: 10%;
	}
	.short-nav .navbar-nav {
		margin: 0 auto;
		width: 570px;
		margin-top: 80px;
		float: none !important;
		padding-left: 3%;
	}
	.main-navbar .navbar-nav > li {
		margin: 0 8px;
	}
}
@media (max-width: 860px) {
	.main-navbar .navbar-nav {
		margin: 0 auto;
		width: 100%;
		margin-top: 80px;
		float: none !important;
        text-align: center;
        padding-left: 0;
	}
	.short-nav .navbar-nav {
		margin: 0 auto;
		width: 480px;
		margin-top: 80px;
		float: none !important;
	}
}
@media (max-width: 767px) {
	.main-navbar .navbar-nav {
		margin-top: 0px;
		width: 100%;
		padding-left: 0;
	}
}

.main-navbar .navbar-nav > li > a {
	padding: 17px 12px;
	font-family: $font-medium;
	font-size: 14px;
	color: $abu;
	text-transform: uppercase;
}
.main-navbar .navbar-nav > li > a.active {
  color: $biru;
  -webkit-text-shadow: 0px 0px 30px $biru5;
  -moz-text-shadow: 0px 0px 30px $biru5;
  text-shadow: 0px 0px 30px $biru5;
}

@media (max-width: 885px){
	.main-navbar .navbar-nav > li {
		margin: 0 0px;
        display: inline-block;
        float: none;
	}
}
.main-navbar .navbar-nav > li > a.signup {
	padding: 17px 27px 17px 32px;
	background: $putih;
	border: 1px solid $biru;
	color: $biru;
	border-radius: 5px;
}
@media (max-width: 1220px){
	.main-navbar .navbar-nav > li > a.signup {
		padding: 17px 20px 17px 20px;
	}
}
.main-navbar .navbar-nav > li > a:hover,
.main-navbar .navbar-nav > li > a:focus {
	color: $biru;
}
.navbar-brand {
	margin-top: -5px;
}
.navbar-brand img {
	width: 25px;
	float: left;
	margin-right: 7px;
}
.navbar-brand h1 {
	float: right;
	font-family: $font-medium;
	font-size: 31.68px;
	color: $biru2;
	margin-top: 0;
}
.navbar-brand h1 span {
	color: $biru3;
}

@media (max-width: 1030px) {
	.navbar-brand {
		position: absolute;
        left: 50%;
        display: block;
        text-align: center;
	}
	.navbar>.container .navbar-brand,
    .navbar>.container-fluid .navbar-brand {
        margin-left: -90px;
    }
}
@media (max-width: 767px) {
	.navbar-brand{
		left: 17.5%;
	}
}
@media (max-width: 540px) {
	.navbar-brand{
		left: 25%;
	}
}
@media (max-width: 400px) {
	.navbar-brand{
		left: 27.5%;
	}
}
@media (max-width: 300px) {
	.navbar-brand{
		left: 30%;
	}
}
.main-navbar .navbar-toggle {
	border:none;
}
.main-navbar .navbar-toggle .icon-bar {
	background-color: $biru2;
}
.main-navbar .navbar-toggle:hover, .main-navbar .navbar-toggle:focus {
	background-color: transparent;
}
@media (max-width: 767px){
	.main-navbar .navbar-collapse, .main-navbar .navbar-form {
		border:none;
		margin-top: 30px;
		padding-bottom: 0;
	}
}