.pagetitle {
	font-family: $font-family;
	font-size: 42px;
	font-weight: 300;
	color: $biru;
	margin-bottom: 20px;
	text-align: center;
}

.ride-form {
	.find-form-wrapper {
		.wraping {
			flex-wrap: nowrap;
			
			@media screen and (max-width: 991px){
				flex-wrap: wrap;	
			}
		}

		.group-input {
			width: auto;

			.field {
				max-width: none;
			}
			
			.find-btn {
				width: 100%;
			}
		}

		.input-leaving,
		.input-going {
			@media screen and (max-width: 991px){
				flex: 1;
			}

			@media screen and (max-width: 767px){
				flex-basis: 100%;
			}
		}

		.switch {
			text-align: center;
			padding-top: 20px;
			padding-bottom: 20px;
			
			@media screen and (max-width: 991px){
				width: auto;
			}
		}

		.input-date {
			@media screen and (max-width: 991px){
				flex: 1 1 100%;
			}
		}

		.ride-btn {

			@media screen and (max-width: 991px){
				margin-left: auto;
				margin-right: auto;
				flex: 1 1 100%;
			}

			@media screen and (max-width: 489px){
				width: 100%;
				margin: 0 9px;
				padding-left: 0;
			}
		}
	}
}


